import React from 'react';
import {Typography, Card, CardContent, Button, LinearProgress } from '@mui/material'
import { makeStyles } from '@mui/styles';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';
import Image from '../../atoms/Image/Image'
import AddIcon from '@mui/icons-material/Add';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import { createTheme, ThemeProvider } from '@mui/material/styles'; 
import OPENBOOK from '../../utils/currentlyReading';
import CLOSEDBOOK from '../../utils/completedReading';

const useStyles = makeStyles(theme => ({
    wrapIconLeft: {
        verticalAlign: 'middle',
        display: 'inline-flex',
        textAlign: 'left',
        paddingTop:'5%'
     },
     wrapIconRight:{
        verticalAlign: 'middle',
        display: 'inline-flex',
        textAlign: 'left',
        paddingTop:'5%',
        paddingLeft:'6%'
     },
    bookName:{
        textAlign: 'left'
    },
    authorName:{
        textAlign: 'left',
        paddingTop:'5%'
    },
    btn:{
     width:'100%',
     color:'secondary.main',
     paddingTop:'10%',
     paddingBottom:'10%'
    }
  })); 
interface Props{
    src:string,
    name:string,
    author:string,
    duration:string,
    noOfReads?:string,
    changeState?(val:number): any,
    dots?:boolean,
    button?:'add'| 'Finished' | 'Read again',
    progressBar:'none'|'full'| 'half',
    id:number,
  changeTab?(val:number):any
}

/* interface BookType{
  id:number,
  src:string,
  name:string,
  author:string,
  duration:string,
  noOfReads?:string,
} */

const innerTheme = createTheme({
  palette: {
    primary: {
      main: '#0365F2',
    },
  },
  typography:{
    button:{
      textTransform:'none'
    }
  }
});
const CardWithButton=(props:Props)=> {
    const classes=useStyles();
    const nextPage=(e: React.MouseEvent<HTMLDivElement, MouseEvent>,val:number )=>{
      if(props.name=== 'Beyond Entrepreneurship'){
      const found = OPENBOOK.some((obj: { name: string; }) => obj.name === 'Beyond Entrepreneurship');
      console.log("found value is: "+found)
      if(!found){
        OPENBOOK.push({"id":10,"src":"beyondEntrepreneur.png","name":"Beyond Entrepreneurship", "author":"Jim Collins & Bill Lazier", "duration":"13"})
        props.changeState && props.changeState(val)
      }}
    }

    const toRepeat=(e: React.MouseEvent<HTMLButtonElement, MouseEvent>,id:number)=>{
      console.log('to read agin')
      const book=CLOSEDBOOK.filter((book: { id: number; }) => book.id=== id)
      console.log(book)
      console.log(OPENBOOK)
      OPENBOOK.push(...book)
      console.log(OPENBOOK)
      const index=CLOSEDBOOK.findIndex((book: { id: number; }) => book.id=== id);
      console.log(index)
      CLOSEDBOOK.splice(index,1)
      console.log(CLOSEDBOOK)
      props.changeTab&& props.changeTab(0)
    }

    const toFinish=(e: React.MouseEvent<HTMLButtonElement, MouseEvent>, nameOfBook:string)=>{
      console.log('to finish')
      const book=OPENBOOK.filter((book: { name: string; })=> book.name=== nameOfBook)
      console.log(book)
      CLOSEDBOOK.push(...book)
      console.log(CLOSEDBOOK)
      const index=OPENBOOK.findIndex((book: { name: string; }) => book.name=== nameOfBook);
      console.log(index)
      OPENBOOK.splice(index,1)
      console.log(OPENBOOK)
      props.changeTab&& props.changeTab(1)
    }

    return (
        <div>
    <Card sx={{ maxWidth: 284 }} onClick={(e)=>nextPage(e,3)} style={{cursor:'pointer'}}>
        <Image path={props.src} width='100%' />
   
        <CardContent>
            <Typography className={classes.bookName} variant="subtitle1">
                {props.name}
            </Typography>

            <Typography className={classes.authorName} variant="body1">
                {props.author}
            </Typography>

              <div style={{display: 'flex', alignItems: 'center', flexWrap: 'wrap',}}>
                <Typography variant="body2" className={classes.wrapIconLeft} >
                    <AccessTimeIcon /> {props.duration}-minute read
                    </Typography>
                    {props.noOfReads &&  
                    <Typography variant="body2" className={classes.wrapIconRight} >  
                    <PersonOutlineOutlinedIcon /> {props.noOfReads}k reads </Typography>
                    }    
            </div>
        </CardContent>
     { props.dots && <MoreHorizIcon style={{marginLeft:'88%'}}/> }
        { props.button==='add' && 
        <ThemeProvider theme={innerTheme}>
        <Button className={classes.btn} sx={{
    ':hover': {
      backgroundColor: 'primary.main', 
      color: '#FFFFFF',
    },
  }}  variant='text' startIcon={<AddIcon/>}>
      <Typography variant='body1'>
          Add to library
          </Typography>
      </Button>
      </ThemeProvider>}



      { props.button==='Finished' && <ThemeProvider theme={innerTheme}>
       <Button className={classes.btn} sx={{
    ':hover': {
      backgroundColor: 'primary.main', 
      color: '#FFFFFF',
    },
  }}  onClick={(e)=>toFinish(e,props.name)} variant='text'>
      <Typography variant='body1'>
          {props.button}
          </Typography>
      </Button> </ThemeProvider>}



      { props.button==='Read again'  && <ThemeProvider theme={innerTheme}>
       <Button className={classes.btn} sx={{
    ':hover': {
      backgroundColor: 'primary.main', 
      color: '#FFFFFF',
    },
  }} onClick={(e)=>toRepeat(e,props.id)} variant='text'>
      <Typography variant='body1'>
          {props.button}
          </Typography>
      </Button> </ThemeProvider>}

      {props.progressBar==='half' && <LinearProgress variant='determinate' value={30} color='info' style={{height: "10px"}}/> }
      
      {props.progressBar==='full' && <LinearProgress variant='determinate' value={100} color='info' style={{height: "10px"}}/> }
      
        </Card> 

        
       


         
      </div>
    );
}

export default CardWithButton;